<template>
    <div>
        <p>Add new  todo <input type="text" v-model="newTodo"><button v-on:click="PushTodo">Push new todo</button></p>
        <ul v-for="(todo, index) in getTodoArray[1]" :key="todo.id">
            
            <li>
                <b-card bg-variant="primary" text-variant="white" :header="'Todo - '+getTodoArray[0]" class="text-center">
                <b-card-text> 
                    <b-form-checkbox class="redbtn"
                        :checked="todo.status" 
                        v-on:change="changeStatus(todo.id, todo.status)"
                    >
                    </b-form-checkbox>
                    <span v-bind:class="{done: todo.status}">
                        {{index+1}} - {{todo.title}}
                    </span>
                </b-card-text>
                </b-card>
                <b-button-close class="close__btn" text-variant="danger" v-on:click="removeTodo(todo.id)"></b-button-close>
            </li>
        </ul>
    </div>
</template>


<script>

import {mapGetters} from "vuex";

export default {
    
    data: () => ({

        paramTodo: "",
        newTodo: "",
        todoList: [],
        todo: [],
    }),

    methods: {
        PushTodo(){

            // console.log("1 "+todoList)
            // this.todo = todoList[this.paramTodo];
            // if(this.todo == "" || this.todo == null){
            //     this.todo = []
            // }
            // console.log("2 "+this.todo)
            this.todoList = this.getTodoArray
            this.todoList[1].unshift({id: Date.now(), title: this.newTodo, status: false})
            let push_temp = JSON.parse(this.getTodoList)
            push_temp[this.paramTodo] = this.todoList

            this.$store.dispatch("PushTodo", [push_temp, this.getUserData.id])
            //this.todoList =  JSON.parse(this.getTodoList)[this.paramTodo];
        },
        removeTodo(id){
            this.todoList = this.getTodoArray
            this.todoList[1] = this.todoList[1].filter(t => t.id !== id)
            let push_temp = JSON.parse(this.getTodoList)
            push_temp[this.paramTodo] = this.todoList
            this.$store.dispatch("PushTodo", [push_temp, this.getUserData.id])
            this.newTodo = ""
            
        },
        changeStatus(id){
            this.todoList = this.getTodoArray
            this.todoList[1].forEach(element => {
                if(element.id == id){
                    element.status = true
                }
            });
            let push_temp = JSON.parse(this.getTodoList)
            push_temp[this.paramTodo] = this.todoList
            this.$store.dispatch("PushTodo", [push_temp, this.getUserData.id])
            this.newTodo = ""
        }
    },

    created() {
        console.log("1")
        this.paramTodo = this.$route.params.id 
        this.$store.commit("updateTodoName", this.paramTodo)
        this.$store.dispatch("checkStatus")
        console.log("2")
        
    },

    mounted(){
        console.log("3")
        
       
        
    },

     computed: mapGetters(['getStatus', 'getUserData', 'getTodoList', 'getTodoArray']),

}
</script>

<style lang="scss" scoped>

    .done {
        text-decoration: line-through;
    }

    li {
        width: 500px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 50px;

        div {
            width: 100%;
        }
    
    }

</style>